@import url("https://fonts.googleapis.com/css?family=Sacramento|Cabin|Pacifico|Permanent+Marker" );


html, body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  margin: 0;
  padding: 0;
    color: $body-color;
  -webkit-font-smoothing: antialiased;
  font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.625;
  	background-color: cornsilk;
}

h1, h2, h3, h4, h5, h6 {
  color: $header-color;
}

Link {
  color: $link-color;
  text-decoration: none;
}

.header {
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
}

.pageBody {
  position: relative;
}
.home{
  margin: 0 auto;
}
.miniHeader {
  background: #393937;
  position: fixed;
  width: 100%;
  z-index: 100;
}
.miniHeaderContents {
  margin: 0 auto;
  max-width: 880px;
  padding: 12px 36px;
  position: relative;
  text-align: right;
}
@media screen and (max-width: 680px) {
  .miniHeaderContents {
    text-align: left;
  }
}
.miniLogo {
  float: left;
  left: -100px;
  top: 5px;
}
.miniLogo > .svg {
  height: 24px;
}

.miniHeaderContents a {
  color: #fff;
  font-weight: bold;
  margin-right: 1em;
  text-decoration: none;
  text-shadow: 0 1px 2px rgba(0,0,0,0.35);
}

.miniHeaderContents a:last-child {
  margin-right: 0;
}

.coverContainer {
  background-color: #393937;
  height: 75vh;
  max-height: 800px;
  min-height: 260px;
  outline: solid 1px rgba(0,0,0,0.28);
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 10000;
}

.cover {
  height: 70vh;
  max-height: 800px;
  min-height: 260px;
  position: absolute;
  width: 100%;
  clip: rect(0, auto, auto, 0);
}

.coverFixed {
  -webkit-align-items: center;
  -webkit-justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  position: fixed;
  width: 100%;
  top: 0;
  height: 70vh;
}

.filler {
  -ms-flex: 10 1;
      flex: 10 1;
  width: 100%;
}

.synopsis {
  -webkit-flex: 10;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex: 10 1;
      flex: 10 1;
  max-width: 700px;
  padding: 0 30px;
  pointer-events: none;
  position: relative;
  width: 100%;
}

.logo {
  bottom: 0;
  left: 60px;
  position: absolute;
  right: 60px;
  top: 0;
}

.logo > .svg {
  height: 100%;
  position: absolute;
  width: 100%;
}
@media screen and (max-width: 680px) {

  .cover {
    max-height: 260px;
  }

  .coverFixed {
    max-height: 260px;
  }

  .miniHeader {
    display: none;
  }

  .miniHeaderContents {
    padding: 12px;
    text-align: center;
  }

  .synopsis {
    max-width: 540px;
  }

  .logo {
    left: 30px;
    right: 30px;
  }

  .contents {
    padding-top: 24px;

    img {
      max-width: 100%
    }
  }

  .pageBody {
    padding: 0 12px;
  }
}



.sectionWrapper{
  max-width: 90em;
  padding-right: 5%;
  padding-left: 5%;
  margin: auto;
}
.header{
	background-color: #393937;
}

.tagline{
  background-color: #EEEFE5;
  padding-top: 2em;
  padding-bottom: 2em;
  padding-left: 5em;
  padding-right: 5em;
  color:#666666;
  font-size: 2em;
  text-align: center;
}
@media screen and (max-width: 680px) {
  .tagline{
    padding:0;
  }
}

.about{
	display: grid;
	grid-template-columns: 3fr 1fr;
}
#AboutTitle{
  height: 50px;
}
.aboutBox{
	grid-area: 1 / 1 / 2 / 2;
  padding-top: 3em;
  padding-left: 4em;
  padding-right: 4em;
}

.aboutImageBox{
	grid-area: 1 / 2 / 1 / 2;


}
.aboutImage{
	width: 100% ;
	max-height: 30em;
}
@media screen and (max-width: 680px) {
  .aboutImageBox{
    grid-area: 2 / 1 / 2 / 2;
  }
  .aboutBox{
    padding:0;
  }
    #AboutTitle{
    margin-left: auto;
  }
}
/*/////////////////////////*/
/*/////////////////////////*/
/*////Skills Container/////*/
/*////////////////////////*/

.skills{
  background-color: #EEEFE5;
  padding-top: 2em;
  height: 14em;
  color:#666666;

}
.skills p{
  font-size: 1.8em;
  text-align: center;
}
.skilllogoWrapper{
  max-width: 800px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.skillCircle{
  background-color: #EEEFE5;
  border-radius: 60px; 
  height: 7em;
  width: 7em;
  background-size: 4em;
  background-position:50% 50%;
  background-repeat:no-repeat; 
}
#node{
background-image: url('../assets/logos/nodejs.png');

}
#firebase{
background-image: url('../assets/logos/firebase.png');

}
#react{
background-image: url('../assets/logos/react.png');

}
#sass{
background-image: url('../assets/logos/sass.png');

}
#webpack{
 background-image: url('../assets/logos/webpack.png');
}

.skillLogos{
  max-width: 3em;
  height: .2em;
}

@media screen and (max-width: 680px) {
  .skills p{
    font-size: 1.3em;
    text-align: center;
  }
  .skillLogos{
    max-width: 1em;
    height: .1em;
  }
  .skilllogoWrapper{
    padding-left: 10px;
  }
  .skillCircle{ 
    height: 3em;
    width: 3em;
    background-size: 2em; 
  }
}

/*/////////////////////////*/
/*/////////////////////////*/
/*////////Projects///////////*/
/*////////////////////////*/
.word{
  padding-top: 10em;
  text-align: center;
}
#poop{
  width: 30em;
}
#turd{
  display:none;
}
@media screen and (max-width: 680px) {
  #poop{
    display:none;
  }
  #turd{
    display:inline;
  }
}

@media screen and (max-width: 680px) {
  #poop{
    display:none;
  }
  #turd{
    display:inline;
  }
}
.projects{
	display: grid;
  margin: auto;
  padding-bottom: 5em;
	grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 20px;
	grid-template-rows: 25em 25em 25em;
}

.projectDescription{
  padding: 2.5em;
  background-color: #EEEFE5;
  text-align: left;
  max-width: 40em;
}
.projectDescription a{
  text-decoration: none;
  text-align: right;
  color:#FC4349;
}

.projectDescription a:visited{
  color:#FC4349;
}
.projectDescription a:hover{
  color:#D0130E;
}
.projectDescription strong{
  font-size: 1.2em;
}
.projectTitle{
  color:#FC4349;
  margin-top: 8em;
  position: relative;
  z-index: 10;   
}

.projectTitle a:visited{
  color:#FC4349;
}

.projectsBox{
  text-align: center;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  overflow: hidden;
  max-width: 40em;  
  background: #2C3E50;
}
.projectsBox a, h1, h4{
  text-decoration: none;
  margin-bottom: 0em;
  margin-top: 0em;
}
.projectsBox img{
  position: relative;
  margin-top: -17em;
  margin-left: -8em;
  width:150%;
  z-index: 0;
  opacity: .3;
  -webkit-transition: 15s ease-in-out;
  -o-transition: 15s ease-in-out;
  transition: 15s ease-in-out;

}
.projectsBox img:hover{  
  -webkit-transform: scale(1.1) rotate(10deg);  
      -ms-transform: scale(1.1) rotate(10deg);  
          transform: scale(1.1) rotate(10deg);
  z-index: 0;
}
#Yello img{
  position: relative;
  z-index: 0;
  opacity: .3;
  -webkit-transition: 15s ease-in-out;
  -o-transition: 15s ease-in-out;
  transition: 15s ease-in-out;
}
#Yello img:hover{  
  -webkit-transform: scale(1.5) rotate(-10deg);  
      -ms-transform: scale(1.5) rotate(-10deg);  
          transform: scale(1.5) rotate(-10deg);
  z-index: 0;
}
.Drooblebox h1{
  font-family:'Sacramento', cursive;
  font-size: 3em;
}
.Drooblebox a:hover{
  color:#D0130E;
  text-shadow: 1px #EEEFE5;
}

.Familychat h1{
  font-family: 'Pacifico', cursive;
}
.Familychat a:hover{
  color:#D0130E;
}

.Yello h1{
  font-family: 'Permanent Marker';
  font-size: 3em;
  font-weight: 2;
}
.Yello a:hover{
  color:#D0130E;  
}

@media screen and (max-width: 680px) {
  .projects{
    padding: 0;
    margin-left: -2.2em;
    margin-right:-2.2em;
    grid-template-columns: 1fr;
    grid-template-rows: 20em 26em 30em;

  }
  .projectDescription{
    padding:5px;
    padding-left: 10px;
  }
  .projectsBox{
    display:none;
  }
}
